<template>
	<view>
		<view class="header">
			<view v-if="!loginStatus" class="lg_button" @click="loginPage">账号登陆</view>
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				loginStatus:false,//登陆状态
				userId:"",
			};
		},
		onLoad() {
			uni.getStorage({
				key:'userId',
				success(res){
					this.userId = res.data;
					this.getUserDetail()
				},
				complete(res){
					console.log('1',res)
				}
			})
			this.$API({
				url:'/login/status',
			}).then(res=>{
				if(res.data.code === 301){
					this.loginStatus = false;
				}else{
					this.loginStatus = true;
				}
			})
		},
		methods:{
			getUserDetail(){
				this.$API({
					url:`/user/detail?uid=${this.userId}`
				})
			},
			loginPage(){
				uni.navigateTo({
					url:'../login/login'
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.header{
		background-color: #d43c33;
		text-align: center;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 300rpx;
		.lg_button{
			width: 280rpx;
			height: 80rpx;
			border-radius: 40rpx;
			background-color: #fff;
			font-size: 36rpx;
			line-height: 80rpx;
			font-weight: bold;
		}
	}

</style>
